 // src/view/liveStreaming/live-streaming-index.vue //直播首页
 <template>
    <div>
        <div class="container">
            <ul class="liveList">
                <li v-for="(item,index) in resData.liveList" :key="index" :style="'background-image:url('+item.imgUrl+')'" @click="enterLive(item.id)">
                    <div class="bgImg">
                        <div v-if="item.liveStatus==0">
                            <div class="liveStatus ing">
                              <span></span>
                              <p>正在直播</p>
                            </div>

                            <div class="content">
                              <p class="theme">{{item.theme}}</p>
                              <p class='title'>{{item.title}}</p>
                              <p class='otherInfo'>{{item.person}} 人参与</p>
                            </div>
                        </div>

                        <div v-else-if="item.liveStatus==1">
                            <div class="liveStatus comeSoon">
                              <span></span>
                              <p>即将直播</p>
                            </div>
                            <div class="content">
                              <p class="theme">{{item.theme}}</p>
                              <p class='title'>{{item.title}}</p>
                              <p class='otherInfo'>{{item.time}}</p>
                            </div>
                        </div>

                        <div v-else-if="item.liveStatus==2">
                            <div class="liveStatus end">
                              <span></span>
                              <p>即将直播</p>
                            </div>
                            <div class="content">
                              <p class="theme">{{item.theme}}</p>
                              <p class='title'>{{item.title}}</p>
                              <p class='otherInfo'>{{item.time}}</p>
                            </div>
                        </div>
                    </div>
                </li>
                
            </ul>
        </div>
        <!-- loading -->
        <div :class="this.isLoading?'showLoad':'hideLoad'" class="shade"  >
           <img class="loading" src="../../assets/svg/Spinner-1s-200px.svg">
        </div>
    </div>
 </template>


 <script>
import appMixins from "@/assets/js/appMixins.js";
export default {
  name: "live-streaming-index",
  mixins: [appMixins],
  components: {},
  data() {
    return {
      isLoading: false,
      resData: {}
    };
  },
  methods: {
    enterLive(id) {
      alert(id);
    },
    getData(data) {
      this.resData = data;
    },
    openLoad() {
      this.isLoading = true;
    },
    closeLoad() {
      this.isLoading = false;
    }
  },
  mounted() {},
  created() {
    let _this = this;
    const resData = {
      liveList: [
        {
          imgUrl:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          liveStatus: 0,
          theme: "",
          title: "apple 春季发布会",
          person: 110,
          time: "",
          id: 110
        },
        {
          imgUrl:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          liveStatus: 1,
          theme: "STAY TRUE",
          title: "魅族 PRO 7 新品发布会",
          person: "",
          time: "明天 23:00 开始",
          id: 110
        },
        {
          imgUrl:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          liveStatus: 2,
          theme: "STAY TRUE",
          title: "魅族 PRO 7 新品发布会",
          person: "",
          time: "于 8 月 23 日结束",
          id: 110
        }
      ]
    };
    this.getData(resData);
    this.openLoad();
    setTimeout(() => {
      _this.closeLoad();
    }, 3000);
  }
};
</script>
<style lang='less' scoped>
@m: 750/375;
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px * @m solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}

.liveList li {
  position: relative;
  width: 100%;
  margin-bottom: 20px * @m;
  height: 220px * @m;
  border-radius: 6px * @m;

  background-repeat: no-repeat;
  background-size: cover;
}

.liveList li .bgImg {
  position: relative;
  z-index: 10;
  background-image: linear-gradient(
    -180deg,
    rgba(15, 37, 64, 0.55) 0%,
    rgba(15, 37, 64, 0.85) 99%
  );
  width: 100%;
  height: 220px * @m;
  border-radius: 6px * @m;
  box-sizing: border-box;
  padding: 20px * @m;
}

.liveList .liveStatus {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
}

.liveList p {
  color: #fff;
}

.title {
  font-size: 20px * @m;
  margin-bottom: 10px * @m;
}

.theme {
  font-size: 20px * @m;
}

.otherInfo {
  font-size: 13px * @m;
}

.content {
  text-align: center;
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 100%;
  padding: 20px * @m;
  transform: translate(-50%, 0);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.liveList .liveStatus span {
  display: inline-block;
  width: 16px * @m;
  height: 16px * @m;
  border-radius: 100%;
  margin-right: 10px * @m;
}

.liveList .liveStatus.ing span {
  background-image: linear-gradient(-180deg, #77ffa6 1%, #4bdb9a 100%);
  box-shadow: 0 8px * @m 10px * @m 0 rgba(118, 204, 251, 0.5),
    inset -1px * @m 2px * @m 3px * @m 0 rgba(255, 255, 255, 0.7);
}

.liveList .liveStatus.comeSoon span {
  background-image: linear-gradient(-180deg, #77cfff 0%, #4b8ddb 100%);
  box-shadow: 0 8px * @m 10px * @m 0 rgba(118, 204, 251, 0.5),
    inset -1px * @m 2px * @m 3px * @m 0 rgba(255, 255, 255, 0.7);
}

.liveList .liveStatus.end span {
  background-image: linear-gradient(-180deg, #ff7777 0%, #dd5a5a 100%);
  box-shadow: 0 8px * @m 10px * @m 0 rgba(251, 118, 118, 0.5),
    inset -1px * @m 2px * @m 3px * @m 0 rgba(255, 255, 255, 0.7);
}


</style>


