import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import liveStreamingIndex from '@/view/liveStreaming/live-streaming-index.vue'; //直播首页

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'liveStreamingIndex',
    component: liveStreamingIndex
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



